@import './vars';

//-----------Button--------------------------------

$btn-height:2.5rem;

$btn-line-height:                1.25 !default;

$btn-disabled-hoverbgcolor:#eceff1;

$btn-secondary: #F2F2F2;
$btn-secondary-enabled-hoverbgcolor:#FFFFFF;
$btn-secondary-enabled-focusbgcolor:#365EBF;

$btn-secondary-gradient-from:#FFFFFF;
$btn-secondary-gradient-to:#F2F2F2;
$btn-secondary-linear-gradient:top, #{$btn-secondary-gradient-from} 0%, #{$btn-secondary-gradient-to} 100%;


$btn-primary: #365EBF;
$btn-primary-enabled-hoverbgcolor:#2546AC;
$btn-primary-enabled-focusbgcolor:#2546AC;

$btn-primary-gradient-from:#3E6CC9;
$btn-primary-gradient-to:#2E50B6;
$btn-primary-linear-gradient:top, #{$btn-primary-gradient-from} 0%, #{$btn-primary-gradient-to} 100%;




$theme2-btn-primary:#52BE7F;
$theme2-btn-primary-enabled-hoverbgcolor:#45AD70;
$theme2-btn-primary-enabled-focusbgcolor:#45AD70;
$theme2-btn-primary-linear-gradient:top, #{$theme2-btn-primary} 0%, #{$theme2-btn-primary} 100%;

$theme2-btn-secondary:#00A9E8;
$theme2-btn-secondary-enabled-hoverbgcolor:#009DD7;
$theme2-btn-secondary-enabled-focusbgcolor:#009DD7;
$theme2-btn-secondary-linear-gradient:top, #{$theme2-btn-secondary} 0%, #{$theme2-btn-secondary} 100%;


$mediav-btn-height:2rem;
$mediav-btn-line-height: 1 !default;
$mediav-btn-primary:#65C178;
$mediav-btn-primary-enabled-hoverbgcolor:#83CC93;
$mediav-btn-primary-enabled-focusbgcolor:#65C178;
$mediav-btn-primary-linear-gradient:top, #{$mediav-btn-primary} 0%, #{$mediav-btn-primary} 100%;

$mediav-btn-secondary:#f0f0f0;
$mediav-btn-secondary-enabled-hoverbgcolor:#f6f6f6;
$mediav-btn-secondary-enabled-focusbgcolor:#e3e3e3;  //border-color
$mediav-btn-secondary-linear-gradient:top, #{$mediav-btn-secondary} 0%, #{$mediav-btn-secondary} 100%;
$mediav-btn-secondary-color:#666666;

$mediav-btn-blue:#009EEB;
$mediav-btn-blue-enabled-hoverbgcolor:#009EEB;
$mediav-btn-blue-enabled-focusbgcolor:#009EEB;
$mediav-btn-blue-linear-gradient:top, #{$mediav-btn-blue} 0%, #{$mediav-btn-blue} 100%;

%button-base{
  outline:none;
  user-select: none;
  vertical-align: middle;
  position: relative;
  cursor: pointer;
  -webkit-font-smoothing: inherit;
}


button {
  @extend %button-base;
}
.btn-block {
  display: block;
  width: 100%;
  + .btn-block {
    margin-top: 0.35rem;
  }
}


@mixin  button_disabled($hoverbgcolor,$opacity:.4){
  &[disabled]{
    cursor:pointer;
    opacity:$opacity;
    &:hover{
      background-color: $hoverbgcolor;
      cursor:not-allowed;
    }
  }
}



@mixin button_enabled($hoverbgcolor ,$focus-bordercolor){
  &:not([disabled]){
    &:hover{
      background:$hoverbgcolor;
    }
    &:active,&:focus,&.focus{
      border-color:$focus-bordercolor;
      //box-shadow: 0 0 0 2px #203c66;
      box-shadow: $btn-active-box-shadow;
    }
  }
}


.btn {
   .ui & {
     //height: $btn-height;
     line-height: $btn-line-height;
     padding-left: $btn-padding-x;
     padding-right: $btn-padding-x;
     padding-top: $btn-padding-y !important;
     padding-bottom: $btn-padding-y !important;
     border-radius: 2px;
     @include button_disabled($btn-disabled-hoverbgcolor);

     //secondary
     &.btn-secondary {
       @include linear-gradient($btn-secondary-linear-gradient);
       @include button_enabled($btn-secondary-enabled-hoverbgcolor,$btn-secondary-enabled-focusbgcolor);
       border: 1px solid #DCDCDC;
       color: $btn-primary;
     }

     //primary
     &.btn-primary {
       color: #FFFFFF;
       border: 0;
       @include linear-gradient($btn-primary-linear-gradient);
       @include button_enabled($btn-primary-enabled-hoverbgcolor, $btn-primary-enabled-focusbgcolor);
     }
   }


  .theme2 & {
      //height: $btn-height;
      line-height: $btn-line-height;
      padding-left: $btn-padding-x;
      padding-right: $btn-padding-x;
      padding-top: $btn-padding-y !important;
      padding-bottom: $btn-padding-y !important;
      border-radius: 2px;
      @include button_disabled($theme2-btn-secondary);
      color: #FFFFFF;
    //secondary
    &.btn-secondary {
      @include linear-gradient($theme2-btn-secondary-linear-gradient);
      @include button_enabled($theme2-btn-secondary-enabled-hoverbgcolor,$theme2-btn-secondary-enabled-focusbgcolor);
      border: 1px solid $theme2-btn-secondary;
    }


    //primary
    &.btn-primary {
      border: 1px solid $theme2-btn-primary;
      @include linear-gradient($theme2-btn-primary-linear-gradient);
      @include button_enabled($theme2-btn-primary-enabled-hoverbgcolor, $theme2-btn-primary-enabled-hoverbgcolor);
      &.btn {
        @include button_disabled($theme2-btn-primary);
      }
    }
  }




  .mediav & {
    line-height: $mediav-btn-line-height;
    padding-left: $btn-padding-x;
    padding-right: $btn-padding-x;
    padding-top: $btn-padding-y !important;
    padding-bottom: $btn-padding-y !important;
    border-radius: 2px;
    @include button_disabled($mediav-btn-secondary);

    //secondary
    &.btn-secondary {
      @include linear-gradient($mediav-btn-secondary-linear-gradient);
      @include button_enabled($mediav-btn-secondary-enabled-hoverbgcolor,$mediav-btn-secondary-enabled-focusbgcolor);
      border: 1px solid $mediav-btn-secondary;
      color:$mediav-btn-secondary-color;
    }


    //primary
    &.btn-primary {
      color: #FFFFFF;
      border: 1px solid $mediav-btn-primary;
      @include linear-gradient($mediav-btn-primary-linear-gradient);
      @include button_enabled($mediav-btn-primary-enabled-hoverbgcolor, $mediav-btn-primary-enabled-hoverbgcolor);
      &.btn {
        @include button_disabled($mediav-btn-primary);
      }
    }


    &.btn-blue {
      color: #FFFFFF;
      border: 1px solid $mediav-btn-blue;
      @include linear-gradient($mediav-btn-blue-linear-gradient);
      @include button_enabled($mediav-btn-blue-enabled-hoverbgcolor, $mediav-btn-blue-enabled-hoverbgcolor);
      &.btn {
        @include button_disabled($mediav-btn-blue);
      }
    }

  }
  //---other theme
}





