$tab-height:32px;

$tab-nav-height:50px;
$tab-container-margin-y: 0.5rem;

.tab-nav-container {
  overflow: hidden;
  position: relative;
  white-space: nowrap;  //
  padding-bottom: .5rem;
  padding-top: .5rem;

  &.tab-nav-container-scrolling {
    padding-left: 5rem;
    padding-right: 5rem;
  }
  .tab-prev,.tab-next {
    position:absolute;
  }

  .tab-nav-wrap {
    overflow: hidden;
    //margin-bottom: -1px;
  }

  .tab-nav-scroll {
     position:relative;
     overflow: hidden;
     white-space: nowrap;
  }
}

.nav-container {
  line-height: $tab-height;
}

%prev-next {
  display: inline-block;
  height:$tab-height;
  width:18px;
  cursor: pointer;
  background-repeat:no-repeat;
  vertical-align: middle;
  position:absolute;
}

.tab-prev {
  //float:left;
  // line-height: 45px;
  left:1rem;
  top:.5rem + $tab-container-margin-y;

  .tab-prev-icon {
    @extend %prev-next;
    //background-image: url('/assets/pmp/arrow-left.png');
  }
}
.tab-next {
  // line-height: 45px;
  top: .5rem + $tab-container-margin-y;
  right: 2rem;
  .tab-next-icon {
    @extend %prev-next;
  //  background-image: url('/assets/pmp/arrow-right.png');
  }
}

.tab-next-icon,.tab-prev-icon {
  >i {
    font-size: 2em;
    font-weight: bold;
  }

  .tab-btn-disabled & {
    //opacity: 0.4;
    color:#E8E8E8;
    cursor: not-allowed!important;
   // color:#ffffff;
   // cursor:none;
  }
}


.tab-bar {
  //float:left;
  /*  height:100%;
    margin-right: 10px;
    border-right: 1px solid #f3f3f3;*/
  outline:none;

  .nav {
    position:relative;
   // text-align: center;
    float: left;
    transform-origin: 0 0;
    &-animated {
      transition: transform 0.5s ease-in-out;
    }
  }

/*  .nav-wrap {
    margin-left: 100px;
    margin-right: 100px;

    width: 80%;
    overflow: hidden;
    height: $tab-nav-height;

  }*/

  .tab {
    display: inline-block;
    height: $tab-nav-height;
    line-height: $tab-nav-height;
    opacity: 0.6;
    >img {
      vertical-align: middle;
      max-width: 120px;
    }

    &:hover {

    }

    &.tab-active {
      opacity: 1;
      > img {
        max-width: 200px;
      }
    }

    &.show {
      //max-height: 9999px;
    }

    &.hide {
      display: none;
      opacity: 0;
      max-height: 0;
    }
  }

}





.content {
   div[role="tabpanel"] {
     &.inactive {
       display:none
     }
   }
}



