$page-disabled-color:#D4D4D4;
$page-color:#666666;

$pagination-bgcolor:#F2F2F2;

$page-active-bgcolor:#65C178;

//creative
$creative-page-border-color:#F2F2F2;

.clearfix:before,
.clearfix:after {
display: table;
content: "";
line-height: 0;
}

.clearfix:after {
clear: both;
}
.clearfix:before, .clearfix:after {
display: table;
content: "";
line-height: 0;
}


.mediav-pagination  {
  margin: 22px 0;
  background: $pagination-bgcolor;
  padding: 9px 2px;

  ul {
    text-align: center;
    li {
      display:inline-block;

      a,span {
        float: left;
        padding: 4px 12px;
        line-height: 1.5;
        text-decoration: none;
      }
    }
  }


  ul li {
/*    &:first-child{
      a,span {
        border-left-width: 1px;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
      }
    }
    &:last-child{
      a,span {
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
      }
    }*/
  }


  &.creative-helper {
    background:#ffffff!important;
    .page {
      padding: 2px 6px;
      border: 1px solid $creative-page-border-color;
    }
    .page.active {
        background: $green-100!important;
    }
  }

  .page {

    font-weight: 600;
    margin-left: 4px;
    min-width: 20px;
    padding: 0 4px;
    color:$page-color;
    background-color: #fff;

    &:not(.disabled){
      cursor: pointer;
    }
    &.disabled {
      background: #fff!important;
      a,span{
        color: $page-disabled-color!important;
        cursor: default;
      }
    }

    &.active{
      background: $page-active-bgcolor!important;
      a,span {
        color: #ffffff;
        cursor: default;
      }
    }

    &:hover {
      background: #ccc;
    }


    &.prev {
      margin-right: 4px;
    }
    &.next {
      margin-left: 4px;
    }
  }

}



