


@mixin box-shadow($shadow...) {
  @if $enable-shadows {
    box-shadow: $shadow;
  }
}

@mixin transition($transition...) {
  @if $enable-transitions {
    transition: $transition;
  }
}



@mixin form-control-focus($color-focus,$bg-focus,$border-focus,$shadow-focus) {
  &:focus,&.focus {
    color: $color-focus;
    background-color: $bg-focus;
    border-color: $border-focus;
    outline: none;
    box-shadow:$shadow-focus;
  }
}



//$input-box-shadow-hover
@mixin form-control-hover($shadow-hover){
  &:hover {
    box-shadow:$shadow-hover;
  }
}


.form-control {
  display: block;
  width: 100%;
  padding: $input-padding-y $input-padding-x;

  @include typography(formcontrol);

  //font-size: $font-size-base;
  //line-height: $input-line-height!important;
  .ui & {
    color: $input-color;
    background-color: $input-bg;
    // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214.
    background-image: none;
    background-clip: padding-box;

    // only   border bottom
    border:none;
    border-bottom: $input-btn-border-width solid $input-border-color;
    border-radius: 2px;

    &[disabled],&.disable{
      cursor: default;
      user-select:none;
      border-color: #DCDCDC;
      background-color: #FFFFFF;
      color: #B8B8B8;
      box-shadow:none;
    }

    @include form-control-hover($input-box-shadow-hover);
    @include form-control-focus($input-color-focus , $input-bg-focus , $input-border-focus  ,$input-box-shadow-focus);
    @include transition(border-color ease-in-out .15s, box-shadow ease-in-out .15s);
  }


  .mediav & {
    color: $mediav-input-color;
    background-color: $mediav-input-bg;
    // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214.
    background-image: none;
    background-clip: padding-box;

    // only   border bottom
    border: $input-btn-border-width solid $mediav-input-border-color;
    border-radius: $mediav-input-border-radius;

    //outline
    outline:none;

    &[disabled],&.disable{
      cursor: default;
      user-select:none;
      border-color: #DCDCDC;
      background-color: #FFFFFF;
      color: #B8B8B8;
      box-shadow:none;
    }
    //@include form-control-hover($input-box-shadow-hover);
   // @include form-control-focus($input-color-focus , $input-bg-focus , $input-border-focus , $input-box-shadow-focus);
    @include transition(border-color ease-in-out .15s, box-shadow ease-in-out .15s);
  }




}

.form-control-lg{
  @include typography(formcontrol-large);
  height:$input-height-lg!important ;
  padding:$input-padding-y-lg $input-padding-x-lg;
}

.form-control-sm {
  @include typography(formcontrol-small);
  height:$input-height-sm!important;
  padding:$input-padding-y-sm $input-padding-x-sm;
}



