.validate-hub{

}

.form-group{
  i.form-label-warn {
    color: #D93B42;
    display:none;
  }
}

.input-required {
  label.form-label ,label.col-form-label {
    &::after{
      content:"*";
      color: #D93B42;
      display: inline-block;
    }
  }
}
.input-error  {


  .form-group {
    i.form-label-warn {
      display: inline-block;
    }
  }

    input,.form-control,.fake-input{
      border-color: #D93B42;  
      &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
          color: #D93B42; opacity:1;
      }

      &::-moz-placeholder { /* Mozilla Firefox 19+ */
          color: #D93B42;opacity:1;
      }
      &:-ms-input-placeholder{
          color: #D93B42;opacity:1;
      }
      &::-webkit-input-placeholder{
          color: #D93B42;opacity:1;
      }
    }

    label {
      color: #D93B42;
    }

  .validate-message-label {
    font-size: 12px;
    display:block;
  }

}


.form-group {
  &.required {
    label {
      &::after{
        content:'*'
      }
    }

    input[required] ,
    textarea[required] ,
    .fake-input{

    }

  }
}



