

$price-color:#FF8500;

$price-height:48px;  //16*3
$border-height: 24px;  //22

$border-x: 12px;

$shadow-color:#8F4B00;
$shadow-width:5px;


$tip-color:#F1A756;



.pmp-price {
  display: inline-block;
  background: $price-color;
  color:#FFFFFF;
  height:$price-height;
  line-height:$price-height;
  position:absolute;
  right: -9px;
  top: 0.05rem;
  &::before {
    content:'';
    display: inline-block;
    border-left:$border-x  solid $price-color;
    border-right:$border-x  solid $price-color;

    border-top:$border-height  solid $price-color;
    border-bottom:$border-height  solid $price-color;

    border-top-color:transparent;
    border-left-color:transparent;

    position: absolute;
    left: $border-x * -2;
  }

  &::after {
    display: inline-block;
    content:'';
    position: absolute;
    bottom: -9px;
    right: 0;

    border-left:$shadow-width  solid $shadow-color;
    border-right:$shadow-width  solid $shadow-color;
    border-top:$shadow-width  solid $shadow-color;
    border-bottom:$shadow-width  solid $shadow-color;

    border-right-color:transparent;
    border-bottom-color:transparent;
  }

  .aw {
    font-size: 32px!important;
  }
}





.pmp-calendar-icon {
  color:#16A9EB;
  cursor: pointer;
  //background-image: url('/assets/pmp/calendar.png');
  //background-repeat:no-repeat;
  vertical-align: middle;
  position: relative;
  top: -0.1rem;
  left: 0.5rem;

  display: inline-block;

 // text-indent: -9999px;

  //<ie11
  height: 1rem;
  width: 2rem;
}


.pmp-item {
  .left {
    float:left;
    img {
      width:220px;
      max-width: 220px;
      //height:140px;
    }
  }

  .right {
    vertical-align: middle;
    display: block;
    position: relative;
    margin-left: 240px;
  }
/*  .left {
    width:30%;
  }
  .right {
    width:70%;
  }*/
  .tip {
    position: relative;
    padding-top: 0.2rem;
   // padding-bottom: 1rem;
    font-size: 13px;
    .tip-item {
      border:1px  solid $tip-color;
      color:$tip-color;
      border-radius:2px;
      padding:2px;
      margin-right: 4px;
    }
  }

  .pmp-description {
   // width:70%;
    @include typography(article);

    word-wrap:break-word ;
    padding-bottom: 1.8rem;
    > p {
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
    }
  }


  .pmp-check {
    font-size:3rem;
   // background-image: url('/assets/pmp/uncheck-square.png');
    display: inline-block;
    height: 35px;
    width: 35px;
    background-repeat:no-repeat;
    vertical-align: middle;
    position:absolute;
    right:2rem;
    top:-.4rem;
    .list-group-item.selected & {
      color:#87CD95;
      //background-image: url('/assets/pmp/check-square.png');
    }
  }


  //response

  @include iphone6-plus {
    .left {
       display: none;
    }
    .right {
      margin-left: 0!important;
      padding-left:1rem !important;
      padding-right:1rem !important;
    }
  }

}



