$item-lineheight:1rem;
$item-selected-bgcolor:#F5F5F5;
$item-selected-bordersize:3px;

//creative helper    .creative-helper & {
$square-checked-flag:0.75em;
$border-width-thick:3px;


$creativehelper-check-width:16px;
.list {
  //reset
  ul,ol,li {
    counter-reset: item;
    list-style: none;
    margin: 0;
    padding: 0;
  }
}




@mixin creativehelper-listitem() {
  .creative-helper & {

    margin: 1rem 1rem;

    height: 70px;
   // min-height: 70px;
    //border: none;

    &:not(.selected){
      border: $item-selected-bordersize solid #ffffff;
    }

    > span ,a{
     // display: inline-block;
      //vertical-center
      top: 50%;
      position: absolute;
      transform: translate(0,-50%);


      padding: 0 3rem;
    }
    background: #FFFFFF!important;

    &:focus,&:hover,&.active ,&.selected  {
      background: #ffffff !important;
    }

    &.selected {
      border: $item-selected-bordersize solid $creativehelper-primary-color;

      &::before {
        content: '';
        display: block;
        position: absolute;
        z-index: 1;

        bottom: 0;
        right: 0;


        border-left: $creativehelper-check-width solid $creativehelper-primary-color;
        border-right:$creativehelper-check-width  solid $creativehelper-primary-color;
        border-top:$creativehelper-check-width  solid $creativehelper-primary-color;
        border-bottom:$creativehelper-check-width  solid $creativehelper-primary-color;
        border-top-color:transparent;
        border-left-color:transparent;


        //@include triangle('left', #3295FF, 24px);
        //transform: rotate(45deg);
      }

      &:after {
        display: block;
        content: '';
        height: ($square-checked-flag / 2);
        width: $square-checked-flag;

        position: absolute;
        z-index: 1;

        bottom: 5px;
        right: -5px;
        transform: translate3d(-50%, -50%, 0) rotate(-45deg);
        border-bottom: $border-width-thick solid #ffffff;
        border-left: $border-width-thick solid #ffffff;
      }
    }

  }
}



.creative-helper.list {
  background: #F3F4F6;
  .list-group {
    padding-bottom: 0;
    padding-top: 0;
    margin-bottom:0;
    margin-top:0;

    @include first-child{
      margin-top: 1rem!important;
      padding-top: 1rem;
    }
    @include last-child {
      margin-bottom: 1rem!important;

      padding-bottom: 2rem;
    }

  }

}


//table-list
.list{
  &.table-list{
    display:table;
  }

  .list-group {
    &.table-listgroup {
      display:table-row;
    }


    .list-group-item {
      &.table-list-item {
        display: table-cell;
      }
    }
  }
}
.table-list-item-cell-cols-2 {
  width:50%;
}

.table-list-item-cell-cols-3 {
  width:33.3333%;
}

.table-list-item-cell-cols-4 {
  width:25%;
}

.list .list-group {
  padding-left: 0;
  padding-right: 1rem;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.075);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.075);
  box-shadow: none;
  color: #404040;
  margin-bottom: 20px;



  .list-group-header{
     .group-header{
       background:#E1EBF4;
       padding:0.2em 1em;
       border-bottom: 2px solid #DCDCDC;
     }
   }

   .list-group-item {
      line-height: $item-lineheight;
      margin-bottom: 0;
      padding: 1em 0;
      vertical-align: top;
      border-left: 0;
      border-right: 0;
      border-top: 0;
      border-bottom: 1px solid #DCDCDC;
      border-radius: 0;
      outline: none;
      cursor: pointer;

     &.selected {
       background: $item-selected-bgcolor;
     }

/*    span {
     // font-family: "HelveticaNeueRoman", Helvetica, Arial, sans-serif;
      font-size: 13px;
      vertical-align: inherit;
      padding-left: 0;
      padding-right: 0;
      outline: 0;
      background: none;
    }*/
    .control {
      display: inline-block;
      margin-left: 20px;
      padding: 0;
      text-align: center;
    }

    &:focus{
       outline: 0;
    }
    &:focus,&:hover,&.active {
      background: #F3F8FE;
    }
  }
}

.list-group-item {
    position: relative;
    display: block;
    margin-bottom: -1px;

  .list .list-group.inline & {

     display: inline-block;
  }


  //default theme
   .ui & {
     border: 1px solid #ddd;
     padding: 10px 15px;
     @include first(1) {
       border-top-right-radius: 4px;
       border-top-left-radius: 4px;
     }
     @include odd {
       background:#F8FAFB;
     }
     @include even {
       background:#FFFFFF;
     }

     &:last-child {
       border-bottom: 0;
     }
   }

  //creation helperlist
   @include creativehelper-listitem;



  .theme2 & {
    @include odd {
      background:#FFFFFF;
    }

    @include even {
      background:#F8FAFB;
    }
  }





}



