.step{
  //reset
  ol {
    position:relative;
    margin:0;
    padding:0;
  }

  &.inline {

    ol {
      line-height: $step-line-height;
      color:#FFFFFF;
      li {
        display: inline-block;
       // vertical-align: middle;  because of no padding
        list-style: none;
        text-align: center;
      }
    }

    li.step-item {
      min-width:15rem;
      background: $blue-100;
      color:$step-item-color;

      &.prev {
        background: $grey-300;
      }

      &.next {
        background: $grey-100;
      }

      &.active {
        color:#FFFFFF;
        + .step-line {
           @include triangle(right,$blue-100,$step-line-height / 2);
        }
      }
    }
    li.step-line{
      position:absolute;
      @include triangle(right,$grey-300,$step-line-height / 2);
      //display:inline-block;
      //border-top:$step-outercircle-size solid #CCCCCC;
      //width:$step-line-width;
    }
  }
}
