$model-zindex: 1050;
$modal-border-width:3px;
$modal-border-color:#666666;
$enable-modal-shadow:false;

.dropback {
  width: 100%;
  height: 100%;
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  opacity: .48;
  z-index: 79;
  background-color: rgba(33,33,33,1.0);
  -webkit-animation: ha-overlay-fade-in 0.3s;
  -moz-animation: ha-overlay-fade-in 0.3s;
  animation: ha-overlay-fade-in 0.3s;
  animation-name: ha-overlay-fade-in;
  animation-duration: 0.3s;
  animation-timing-function: initial;
  animation-delay: initial;
  animation-iteration-count: initial;
  animation-direction: initial;
  animation-fill-mode: forwards;
  animation-play-state: initial;
  -webkit-animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}


.modal {
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: $model-zindex;
  -webkit-overflow-scrolling: touch;
  outline: 0;



  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  -ms-flex-direction: column;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  -ms-flex-pack: justify;
  justify-content: center;


  .modal-dialog {
    position: relative;
    width: auto;
    margin: 10px;
    &.small {
      width: 50%;
      min-width: 380px;
    }

    &.medium {
      width: 70%;
      min-width: 530px;
    }
    &.large {
      width: 80%;
      min-width: 600px;
    }
  }

  .modal-container {
    position: relative;
    background-color: #fff;
    border: $modal-border-width solid $modal-border-color;
   // border-radius: 6px;

    background-clip: padding-box;
    outline: 0;
    min-width: 240px;


    @if $enable-modal-shadow {
      -webkit-box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
      box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
      @media (min-width: 768px) {
        -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
      }
    }


  }



  .modal-container {

    header {

      background-color: #f6f6f6;
      position: relative;
      padding: 0.5rem 0.8rem;

      .message-close{
        cursor: pointer;
      }
      .modal-title {
        line-height: 1.4;
        font-weight: bold;
        font-size: 1.5em;
      }
    }

    section {
      padding: 0;
      margin: 20px 30px 25px 30px;
    }

    footer {
      margin: 0 30px 0 30px;
      padding: 0 0 30px 0;
      text-align: right;
      border-top: 1px solid #DCDCDC;
      button {
        margin: 20px 0 0 0;
      }
    }
  }


  &.show{
    -webkit-animation: fade-in 0.3s;
    -moz-animation: fade-in 0.3s;
    animation: fade-in 0.3s;
    -webkit-animation-fill-mode: forwards;
    -moz-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
  }

  &.modal-open{
    overflow-x: hidden;
    overflow-y: auto;
  }



  .modal-action{
    position: relative;
    background-color: #fff;
    border: 1px solid #999;
    border-radius: 6px;
    -webkit-box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
    box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
    background-clip: padding-box;
    outline: 0;
  }

  .modal-content {
    outline:0;
  }

}







.modal.fade .modal-dialog {
  -webkit-transform: translate3d(0, -25%, 0);
  transform: translate3d(0, -25%, 0);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  -moz-transition: -moz-transform 0.3s ease-out;
  -o-transition: -o-transform 0.3s ease-out;
  transition: transform 0.3s ease-out; }
.modal.in .modal-dialog {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}


.modal-backdrop.fade {
  opacity: 0;
  filter: alpha(opacity=0);
}
.modal-backdrop.in {
  opacity: 0.5;
  filter: alpha(opacity=50);
}



