$color-background-scrollbar:#f0f0f0;
$color-background-scrollbar-track:#f0f0f0;
$border-radius-large:10px;
$border-width-thin:8px;
@mixin scrollbar($size: 10px, $thumb: $color-background-scrollbar, $track: $color-background-scrollbar-track) {

  &::-webkit-scrollbar {
    width: $size;
    height: $size;

    &:window-inactive {
      opacity: 0;
    }
  }

  &::-webkit-scrollbar-thumb {
    background: $thumb;
    border-radius: $border-radius-large;
    box-shadow: $thumb 0 0 0 $border-width-thin inset;
  }

  &::-webkit-scrollbar-track {
    background: $track;
  }
}

// Enable momentum scrolling in Mobile Safari (iOS)
// Side effect: hides scrollbars for this browser
//
// Fixes a bug where some zones were not scrollable:
// https://github.com/salesforce-ux/design-system/issues/81
@mixin enable-ios-momentum-scrolling {
  -webkit-overflow-scrolling: touch;
}


.scrollable {
  @include scrollbar;
  @include enable-ios-momentum-scrolling;
  overflow: auto;
}


.scrollable-x {
  @include scrollbar;
  @include enable-ios-momentum-scrolling;
  max-width: 100%;
  overflow: hidden {
    x: auto;
  };
}

.scrollable-y {
  @include scrollbar;
  @include enable-ios-momentum-scrolling;
  max-height: 100%;
  overflow: hidden {
    y: auto;
  };
}




.listitem-scroll-style{
  @include scrollbar(8px, #BACBE1, #eef1f6);

  &::-webkit-scrollbar-thumb:vertical
  {
    height: 5px;
   // background-color: #999999;
   // -webkit-border-radius: 6px;
  }
}

.datatable-scroll-style{
  @include scrollbar(8px, #9faab5, #eef1f6);
}

.splitleft-scroll-style{
  @include scrollbar(8px, #D6D9DC, #EFF0F1);
  max-height: 100%;
  height:100%;
  overflow: hidden {
    y: scroll;
  };
}



