.pmp-calendar {
  border: 1px solid #CBCBCB;
}

.pmp-calendar table {
  border-collapse: collapse;
}

.pmp-calendar .calendar-month thead {
  background: #666666;
  color: #FFFFFF;
}

.pmp-calendar .calendar-month thead tr > th {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
}

.pmp-calendar .calendar-month th > abbr {
  text-decoration: none;
}

.pmp-calendar .calendar-month tbody tr > td {
  padding-top: 0.65rem;
  padding-bottom: 0.65rem;
  padding-left: 1rem;
  padding-right: 1rem;
  text-align: center;
}

.pmp-calendar .calendar-month tbody .disabled-text {
  background: #F2F2F2;
  color: #A4A4A4;
}

.pmp-calendar .calendar-month tbody .calendar-selected-date {
  background: #019FE9;
  color: #FFFFFF;
}

.pmp-calendar .calendar-month tbody .calendar-selected-date .pmp-calendar-enable-date {
  color: #FFFFFF;
}

.pmp-calendar .calendar-month tbody .pmp-calendar-enable-date {
  color: #009FE9;
}

.pmp-price {
  display: inline-block;
  background: #FF8500;
  color: #FFFFFF;
  height: 48px;
  line-height: 48px;
  position: absolute;
  right: -9px;
  top: 0.05rem;
}

.pmp-price::before {
  content: '';
  display: inline-block;
  border-left: 12px solid #FF8500;
  border-right: 12px solid #FF8500;
  border-top: 24px solid #FF8500;
  border-bottom: 24px solid #FF8500;
  border-top-color: transparent;
  border-left-color: transparent;
  position: absolute;
  left: -24px;
}

.pmp-price::after {
  display: inline-block;
  content: '';
  position: absolute;
  bottom: -9px;
  right: 0;
  border-left: 5px solid #8F4B00;
  border-right: 5px solid #8F4B00;
  border-top: 5px solid #8F4B00;
  border-bottom: 5px solid #8F4B00;
  border-right-color: transparent;
  border-bottom-color: transparent;
}

.pmp-price .aw {
  font-size: 32px !important;
}

.pmp-calendar-icon {
  color: #16A9EB;
  cursor: pointer;
  vertical-align: middle;
  position: relative;
  top: -0.1rem;
  left: 0.5rem;
  display: inline-block;
  height: 1rem;
  width: 2rem;
}

.pmp-item {
  /*  .left {
    width:30%;
  }
  .right {
    width:70%;
  }*/
}

.pmp-item .left {
  float: left;
}

.pmp-item .left img {
  width: 220px;
  max-width: 220px;
}

.pmp-item .right {
  vertical-align: middle;
  display: block;
  position: relative;
  margin-left: 240px;
}

.pmp-item .tip {
  position: relative;
  padding-top: 0.2rem;
  font-size: 13px;
}

.pmp-item .tip .tip-item {
  border: 1px solid #F1A756;
  color: #F1A756;
  border-radius: 2px;
  padding: 2px;
  margin-right: 4px;
}

.pmp-item .pmp-description {
  font-family: Roboto, sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-size: 1rem;
  font-weight: 500;
  word-wrap: break-word;
  padding-bottom: 1.8rem;
}

.pmp-item .pmp-description > p {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.pmp-item .pmp-check {
  font-size: 3rem;
  display: inline-block;
  height: 35px;
  width: 35px;
  background-repeat: no-repeat;
  vertical-align: middle;
  position: absolute;
  right: 2rem;
  top: -.4rem;
}

.list-group-item.selected .pmp-item .pmp-check {
  color: #87CD95;
}

@media only screen and (min-device-width: 414px) and (max-device-width: 736px) and (-webkit-device-pixel-ratio: 3) {
  .pmp-item .left {
    display: none;
  }
  .pmp-item .right {
    margin-left: 0 !important;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
  }
}

.ceative-helper-main {
  min-height: 400px;
}

.ceative-helper-main p.tip-text {
  text-align: center;
}

.creative-helper-component .question-icon {
  font-size: 0.6rem;
  padding: 0 10px;
  position: relative;
  top: -0.1rem;
}

img.mediav-img {
  position: relative;
  /*    width:450px;
    height:200px;*/
  display: inline-block;
  overflow: hidden;
  background: #fff;
  border: 5px solid #fff;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.3);
}

img .img-round {
  /*    &:hover {

      -moz-border-radius:50%;
      -webkit-border-radius:50%;
      -o-border-radius:50%;
      border-radius:50%
    }*/
  transition: all .2s ease-in-out;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -ms-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;
}

button {
  outline: none;
  user-select: none;
  vertical-align: middle;
  position: relative;
  cursor: pointer;
  -webkit-font-smoothing: inherit;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-block + .btn-block {
  margin-top: 0.35rem;
}

.ui .btn {
  line-height: 1.25;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem !important;
  padding-bottom: 0.5rem !important;
  border-radius: 2px;
}

.ui .btn[disabled] {
  cursor: pointer;
  opacity: 0.4;
}

