  -webkit-animation: fade-in 0.3s;
  -moz-animation: fade-in 0.3s;
  animation: fade-in 0.3s;
  -webkit-animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.modal.modal-open {
  overflow-x: hidden;
  overflow-y: auto;
}

