
$padding-x:1rem;
$padding-y:.65rem;

$pmp-calendar-border-color:#CBCBCB;
.pmp-calendar {


  border:1px solid $pmp-calendar-border-color;


  table{border-collapse:collapse;}

  .calendar-month {
    thead {
      background: #666666;
      color:#FFFFFF;
      tr > th {
        padding-left:1rem;
        padding-right:1rem;
        padding-top:0.4rem;
        padding-bottom:0.4rem;
      }
    }


    th > abbr {
      text-decoration: none;
    }



    tbody {
      tr > td {
        padding-top:$padding-y;
        padding-bottom:$padding-y;
        padding-left:$padding-x;
        padding-right:$padding-x;
        text-align:center;
      }
      .disabled-text {
          background: #F2F2F2;
          color:#A4A4A4;
      }

      .calendar-selected-date {
        background:#019FE9;
        color:#FFFFFF;

        .pmp-calendar-enable-date{
          color:#FFFFFF;
        }
      }
      .pmp-calendar-enable-date {
        color:#009FE9;
      }


    }
  }


}


