// dropdown  timepickerinput input

$switcher-width: 16px;
$switcher-margin: 8px;
$switch-flag:8px;

.dropdown,.datepicker-input{
  &.fake-input{
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #FFFFFF), color-stop(100%, #F2F2F2));
    background: -webkit-linear-gradient(top, #FFFFFF 0%, #F2F2F2 100%);
    background: -ms-linear-gradient(top, #FFFFFF 0%, #F2F2F2 100%);
    background: linear-gradient(to bottom, #FFFFFF 0%, #F2F2F2 100%);
  }
}
.fake-input {
  display: inline-block;
  width: 100%;
  min-height: 2.6em;
  border-color: transparent;
  //border-bottom: 2px solid #f0f0f0;
  overflow: hidden;
  border-radius: 2px;
  border: 1px solid #C7C7C7;
  outline: none;
  padding: $input-padding-y $input-padding-x;
  font-size: $font-size-base;
  line-height: $input-line-height;
  background: #FFFFFF;
  &:hover{
    background: #FFFFFF;
  }

  .text {
    display: inline-block;
    outline: 0;
    // min-width:200px
    width: 100%;
  }

  .icon-right{
    float:right;
    position:relative;
    width: $switch-flag *2;
    height: $switch-flag *2;
  }
}


.fake-input-lg{
  font-size: $font-size-lg!important;
  line-height: $line-height-lg!important;
  height:$input-height-lg!important ;
}

.fake-input-sm {
  font-size: $font-size-sm!important;
  line-height: $line-height-sm!important;
  height:$input-height-sm!important;
}



